import common from "../../../common/common.js";

export default {
	
	data() {
		return {
			form_error:""
		}
	},
	methods: {
		//用户注册流程
		formSubmit:function(e){
			var serverUrl = common.serverUrl
			var username = e.detail.value.username
			var password = e.detail.value.password
			var psd = e.detail.value.psd
			
			if(username !='' & password !=''){
				if(username.length >= 2 & password.length >=6){
					if(password === psd){
						this.form_error = ''
							
							//把数据写入服务器，并且返回是否成功
							uni.request({
								url: serverUrl+'/wx_check_reg_yonghu.php',
								method: 'GET',
								data: {
									yhm: username,
									mm: password
								},
								success: res => {
									// console.log(res.data)
									if(res.data.zt == 'yes'){
										
										//写入数据库成功，再写入缓存
										uni.setStorage({
										    key: 'u_login',
										    data: 'ok',
										    success: function () {
										        console.log('u_login写入缓存');
										    }
										});
										
										uni.setStorage({
										    key: 'u_id',
										    data: res.data.uid,
										});
										
										uni.setStorage({
										    key: 'username',
										    data: username,
										    success: function () {
										        // 注册成功，跳转用户中心
										        uni.switchTab({
										        	url: '/pages/me/me'
										        });
										    }
										});
										
									}else if(res.data.zt == 'no'){
										var form_error = '注册失败:'+ res.data.xinxi
										this.form_error = form_error
									}
								}
							});
						
					}else{
						var form_error = "两次密码输入不一致"
						this.form_error = form_error
					}
				}else{
					var form_error = "账号密码长度不对"
					this.form_error = form_error
				}
			}else{
				var form_error = "账号或者密码不能为空"
				this.form_error = form_error
			}
		}

	}
}
